import Vue from 'vue'
// import App from './App.vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import Home from './paginas/Home.vue'
const Contato = { template: '<p> Pgina de Contato </p>'}
const SemPagina = { template: '<p>Essa pgina no existe! </p>'}

const routes = [
  { path: '/', component: Home },
  { path: '/contato', component: Contato }
]

const router = new VueRouter({
  routes  
})

const app = new Vue({
  router
}).$mount('#app')



